import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

const store = new Vuex.Store({
    // 定义状态
    state: {
        author: 'Wise Wrong',
        class: '高一一班'
    },
    mutations: {
        newAuthor(state, msg) {
            state.author = msg
        },
        newClass(state, msg) {
            state.class = msg
        }
    }
})

export default store


