<template>
	<div v-loading="loading_1" element-loading-text="拼命加载中">
		<button class='datas' @click='myData'>点击此处获取数据请求</button>
		<el-table :data="mytodos1" style="width: 100%">
			<el-table-column prop="author.loginname" label="姓名">
			</el-table-column>
			<el-table-column label="头像" prop="author.avatar_url">
			</el-table-column>
			<el-table-column label="操作">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--<li v-for="item of mytodos">
									<p>{{item.author.loginname}}</p>
									<p>{{item.author.avatar_url}}</p>
									<img :src="item.author.avatar_url" alt="">
								</li>-->
		<div class="block">
			<span class="demonstration">完整功能</span>
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[5, 10, 15, 20]" :page-size="5" layout="total, sizes, prev, pager, next, jumper" :total="mytodos.length">
			</el-pagination>
		</div>
	</div>
</template>

<script>

export default {
	data() {
		return {
			mytodos: [],
			loading_1: false,
			page_size: 5,
			mytodos1: [],
			currentPage: "",
			star: 0,
			end: 5
		}
	},
	methods: {
		myData: function () {
			this.$axios({
				method: 'GET',
				url: 'https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312'
			}).then(function (datas) {
				this.mytodos = datas.data.data.replies;
				// console.log(this.mytodos)
				this.mytodos1 = this.mytodos.slice(this.star, this.end)
				this.loading_1 = false;
				console.log(datas)
				// console.log(this.mytodos)
				// this.$store.dispatch('incrementAsync',mytodos) //异步处理
			}.bind(this));
			let params = {};
			this.$api.get('topic/5433d5e4e737cbe96dcef312', params, function (r) {
				// v.lists = r.data
				console.log(1)
				console.log(r)
			})
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			this.page_size = val;
			// console.log(this.page_size)
			this.star = (this.currentPage - 1) * this.page_size;
			this.end = this.star + this.page_size;
			this.mytodos1 = this.mytodos.slice(this.star, this.end)
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.currentPage = val;
			this.star = (val - 1) * this.page_size;
			this.end = this.star + this.page_size;
			this.mytodos1 = this.mytodos.slice(this.star, this.end)
		}
	},
	computed: {
		// count() {   //vuex数据抓取   
		// 	return this.$store.state.count
		// },
		// 	myData() {
		// 		// console.log(this.mytodos)
		// 		// console.log(this)
		// 		var _this = this;
		//   _this.loading_1 = true
		// 		axios({
		// 			method: 'GET',
		// 			url: 'https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312'
		// 		})
		// 			.then(function (datas) {
		// 				_this.mytodos = datas.data.data.replies;
		// 				// console.log(_this.mytodos)
		// 				      _this.loading_1 = false;
		// 				console.log(datas)
		// 				console.log(_this.mytodos)
		// 				// this.$store.dispatch('incrementAsync',mytodos) //异步处理
		// 			})
		// 	}
	}
	// mapState ({ //vuex数据抓取 多个参数使用 2种方法可选
	// 	count: state => state.count 
	// })

}

</script>


<style>
.datas {
	width: 100%;
	height: 50px;
	background: #846216;
	text-align: center;
	line-height: 50px;
	border: none;
	color: #fff;
}

.mylitodos li {
	text-align: center;
	width: 100%;
	margin-top: 10px;
	background: #ccc;
	color: red;
	list-style: none;
}

li {
	list-style: none
}
</style>


