<template>
    <section>
        <!--工具条-->
        <!--栅格布局 -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <div class="block">
                <span class="demonstration"> click 1113123</span>
                <!--默认click-->
                <el-cascader expand-trigger="click"   change-on-select :options="options" v-model="selectedOptions" @change="handleChange">
                </el-cascader>
            </div>
            <!--filters 过滤搜索-->
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="姓名"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary">新增</el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <!--显示列表-->
        <div v-loading="loading_1" element-loading-text="拼命加载中">
            <!--<button class='datas' @click='myData'>点击此处获取数据请求</button>-->
            <el-table :data="mytodos1" style="width: 100%">
                <el-table-column prop="id" label="姓名">
                    <!--<el-table-column prop="author.loginname" label="姓名">-->
                </el-table-column>
                <el-table-column label="头像" prop="create_at">
                    <!--<el-table-column label="头像" prop="author.avatar_url">-->
                </el-table-column>
                <el-table-column label="头像" prop="create_at">
                </el-table-column>
                <el-table-column label="头像" prop="create_at">
                </el-table-column>
                <el-table-column label="头像" prop="create_at">
                </el-table-column>
                <el-table-column label="操作">
                    <template scope="scope">
                        <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button size="small" type="danger" @click="handleDel(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--<li v-for="item of mytodos">
                                                                        						<p>{{item.author.loginname}}</p>
                                                                        						<p>{{item.author.avatar_url}}</p>
                                                                        						<img :src="item.author.avatar_url" alt="">
                                                                        </li>-->
            <!--编辑页面-->
            <el-dialog title="编辑" v-model="dialogVisible" :close-on-click-modal="false">
                <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="editForm.id" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="地址">
                        <el-input type="textarea" v-model="editForm.create_at"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button :plain="true" type="info" @click="dialogVisible = false">确 定</el-button>
                </span>
            </el-dialog>
    
            <div class="block">
                <span class="demonstration">完整功能</span>
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[5, 10, 15, 20]" :page-size="5" layout="total, sizes, prev, pager, next, jumper" :total="mytodos.length">
                </el-pagination>
            </div>
        </div>
    </section>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
export default {
    data() {
        return {
            filters: {
                name: ''
            },
            mytodos: [],
            loading_1: false,
            page_size: 5,
            mytodos1: [],
            currentPage: "",
            star: 0,
            end: 5,
            editFormRules: {
                name: [
                    { required: false, message: '请输入姓名', trigger: 'blur' }
                ]
            },
            dialogVisible: false,//编辑页面是否显示
            //编辑页数据
            editForm: {
                id: '',
                create_at: ''
            },
            options: [
                {
                    value: '1',
                    label: '广东省',
                    children: [{
                        value: '1',
                        label: '深圳市',
                        children: [
                            {
                                value: '1',
                                label: '宝安区'
                            }, {
                                value: '2',
                                label: '反馈'
                            }, {
                                value: '3',
                                label: '效率'
                            }, {
                                value: '4',
                                label: '可控'
                            }]
                    },
                    {
                        value: '2',
                        label: '广州市',
                        children: [
                            {
                                value: '21',
                                label: '宝安区'
                            }, {
                                value: '22',
                                label: '反馈'
                            }, {
                                value: '23',
                                label: '效率'
                            }, {
                                value: '24',
                                label: '可控'
                            }]
                    }
                    ],
                },
                {
                    value: '2',
                    label: '江西省',
                    children: [{
                        value: '3',
                        label: '樟树市',
                        children: [{
                            value: '31',
                            label: '区'
                        }, {
                            value: '32',
                            label: '反馈'
                        }, {
                            value: '33',
                            label: '效率'
                        }, {
                            value: '34',
                            label: '可控'
                        }]
                    },
                    {
                        value: '4',
                        label: '宜春市',
                        children: [{
                            value: '41',
                            label: '区'
                        }, {
                            value: '42',
                            label: '反馈'
                        }, {
                            value: '43',
                            label: '效率'
                        }, {
                            value: '44',
                            label: '可控'
                        }]
                    }],
                },
            ],
            selectedOptions: [],
        }
    },
    methods: {
        myData() {
            // console.log(this.mytodos)
            // console.log(this)
            var _this = this;
            _this.loading_1 = true
            axios({
                method: 'GET',
                url: 'https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312'
            })
                .then(function (datas) {
                    _this.mytodos = datas.data.data.replies;
                    // console.log(_this.mytodos)
                    _this.mytodos1 = _this.mytodos.slice(_this.star, _this.end)
                    _this.loading_1 = false;
                    // console.log(datas)
                    // console.log(_this.mytodos)
                    // this.$store.dispatch('incrementAsync',mytodos) //异步处理
                })
        },
        //显示编辑界面
        handleEdit: function (index, row) {
            this.dialogVisible = true;
            // console.log(this.editForm)
            this.editForm = Object.assign({}, row);
            // console.log(this.editForm)
        },
        //删除
        handleDel: function (index, row) {
            this.$confirm('确认删除该记录吗?', '提示', {
                type: 'warning'
            })
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        }
        ,
        //分页方法
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.page_size = val;
            // console.log(this.page_size)
            this.star = (this.currentPage - 1) * this.page_size;
            this.end = this.star + this.page_size;
            this.mytodos1 = this.mytodos.slice(this.star, this.end)
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
            this.star = (val - 1) * this.page_size;
            this.end = this.star + this.page_size;
            this.mytodos1 = this.mytodos.slice(this.star, this.end)
        },
        testJS: () => {
        },
        handleChange(value) {
            console.log(value);
        }
    },
    computed: {
        // count() {   //vuex数据抓取   
        // 	return this.$store.state.count
        // },
        // 	myData() {
        // 		// console.log(this.mytodos)
        // 		// console.log(this)
        // 		var _this = this;
        //   _this.loading_1 = true
        // 		axios({
        // 			method: 'GET',
        // 			url: 'https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312'
        // 		})
        // 			.then(function (datas) {
        // 				_this.mytodos = datas.data.data.replies;
        // 				// console.log(_this.mytodos)
        // 				      _this.loading_1 = false;
        // 				console.log(datas)
        // 				console.log(_this.mytodos)
        // 				// this.$store.dispatch('incrementAsync',mytodos) //异步处理
        // 			})
        // 	}
    },
    mounted() {
        //调用方法
        this.myData();
        this.testJS();
    }
    // mapState ({ //vuex数据抓取 多个参数使用 2种方法可选
    // 	count: state => state.count 
    // })

}

</script>


<style>
.datas {
    width: 100%;
    height: 50px;
    background: #846216;
    text-align: center;
    line-height: 50px;
    border: none;
    color: #fff;
}

.mylitodos li {
    text-align: center;
    width: 100%;
    margin-top: 10px;
    background: #ccc;
    color: red;
    list-style: none;
}

li {
    list-style: none
}
</style>


