<template>
	<div v-loading="loading_1" element-loading-text="拼命加载中">
		<button class='datas' @click='myData'>点击此处获取数据请求</button>
		<ul class="content_ul">
			<li>
				<span>头像</span>
				<span>姓名</span>
				<span>地址</span>
			</li>
			<li v-for="item of mytodos_fy">
				<span>
					<img style="width:60px;height:60px" :src="item.author.avatar_url" alt="">
				</span>
				<span>{{item.author.loginname}}</span>
				<span>{{item.author.avatar_url}}</span>
			</li>
		</ul>
		<div class="block">
			<el-pagination @current-change='pagination' :page-size="10" layout="prev, pager, next" :total="mytodos.length">
			</el-pagination>
		</div>
	</div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'

export default {
	data() {
		return {
			mytodos: [],
			mytodos_fy: [],
			loading_1: false
		}
	},
	methods: {
		myData() {
			var _this = this;
			_this.loading_1 = true
			axios({
				method: 'GET',
				url: 'https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312'
			})
				.then(function (datas) {
					_this.mytodos = datas.data.data.replies;
					// console.log(_this.mytodos)
					_this.mytodos_fy = _this.mytodos.slice(0, 9)
					_this.loading_1 = false;
					console.log(datas)
					console.log(_this.mytodos)
					// this.$store.dispatch('incrementAsync',mytodos) //异步处理
				})
		},
		pagination(val) {
			var _this = this;
			this.loading_1 = true
			console.log(`当前页: ${val}`);
			var star = (val - 1) * 10;
			var end = star + 9;
			this.mytodos_fy = this.mytodos.slice(star, end);
			setTimeout(() => {
				this.loading_1 = false
			}, 200);
			// setTimeout(function(){
			// 	_this.loading_1 = false
			// }, 500);
		}
	},
	computed: {
		count() {   //vuex数据抓取   
			return this.$store.state.count
		},

	}
	// mapState ({ //vuex数据抓取 多个参数使用 2种方法可选
	// 	count: state => state.count 
	// })

}

</script>

<style  scoped lang="scss">
.content_ul {
	padding: 0px;
	li {
		border: 1px silver solid;
		span {
			margin: 0px;
			padding: 0px;
			display: inline-block;
			width: 30%;
			height: 60px;
			border-left: 1px silver solid;
			text-align: center;
			vertical-align: middle;
			img {
				vertical-align: middle;
				border-radius: 50%;
			}
		}
	}
}

.datas {
	width: 100%;
	height: 50px;
	background: #846216;
	text-align: center;
	line-height: 50px;
	border: none;
	color: #fff;
}

.mylitodos li {
	text-align: center;
	width: 100%;
	margin-top: 10px;
	background: #ccc;
	color: red;
	list-style: none;
}

li {
	list-style: none
}
</style>


