<template>
<div>
  <div class="block">
    <span class="demonstration">默认 Hover 指示器触发</span>
    <el-carousel  height="150px">
      <el-carousel-item v-for="item in 4" :key="item">
        <h3>{{ item }}</h3>
      </el-carousel-item>
    </el-carousel>
  </div>
  <div class="block">
    <span class="demonstration">Click 指示器触发</span>
    <el-carousel trigger="click" height="150px">
      <el-carousel-item v-for="item in mytodos" :key="item">
        <h3>{{ item.content }}</h3>
      </el-carousel-item>
    </el-carousel>
  </div>
  </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'

export default {
	data(){
		return{
			mytodos:[]
		}
	},
    created(){
        // console.log(this.mytodos)
      // console.log(this)
      var _this = this;
		axios({
				method:'GET',
				url:'https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312'
			})
			.then(function(datas){
        _this.mytodos = datas.data.data.replies;
        // console.log(_this.mytodos)
        console.log(datas)
        console.log(datas.data.data)
				// this.$store.dispatch('incrementAsync',mytodos) //异步处理
			})
	},

	// mapState ({ //vuex数据抓取 多个参数使用 2种方法可选
	// 	count: state => state.count 
	// })
	
}
	
</script>




<style>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
    text-align: center;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
</style>


