import Vue from 'vue';
import Router from 'vue-router';


// 公共模板
import home from '../page/home.vue'
import login from '../page/login.vue'

//nav1
import index from '../page/nav1/index.vue'
import content from '../page/nav1/content.vue'
import axiosTest from '../page/nav1/axiosTest.vue'
import layout from '../page/nav1/layout.vue'
import echart from '../page/nav1/echart.vue'
import Vuex from '../page/nav1/vuex.vue'
//nav2
import table1 from '../page/nav2/table1.vue'
import table2 from '../page/nav2/table2.vue'
import upload from '../page/nav2/upload.vue'
import vueImgInputer from '../page/nav2/vue-img-inputer.vue'

//学校管理
const school = resolve => require(['../page/userManage/school'], resolve)
//
const classList = resolve => require(['../page/userManage/classList'], resolve)
//
const customerService = resolve => require(['../page/userManage/customerService'], resolve)
const company = resolve => require(['../page/userManage/company'], resolve)
//业务管理
const client = resolve => require(['../page/businessManage/client'], resolve)
const message = resolve => require(['../page/businessManage/message'], resolve)
const card = resolve => require(['../page/businessManage/card'], resolve)
//系统管理
const commit = resolve => require(['../page/systemManage/commit'], resolve)
const personal = resolve => require(['../page/systemManage/personal'], resolve)
const feedback = resolve => require(['../page/systemManage/feedback'], resolve)
//权限管理
const distribute = resolve => require(['../page/permission/distribute'], resolve)
//监控管理
const terminal = resolve => require(['../page/monitoring/terminal'], resolve)
//数据管理
const dataCheck = resolve => require(['../page/dataManage/dataCheck'], resolve)
const statisticalForm = resolve => require(['../page/dataManage/statisticalForm'], resolve)


import NotFound from '../page/elementUI/stration.vue'

// 第三方
import Ueditor from '../components/Ueditor.vue'
Vue.use(Router);
//所有权限通过路由表
//一些不需要权限的公用页面
const routers = [
    {
        path: '/login',
        component: login,
        hidden: true  //hidden为自定义属性
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    // { path: '/main', component: Main },
    //所有子页面 都基于Home页面下 
    {
        path: '/',
        component: home,
        name: '用户管理',
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/school', component: school, name: '学校管理', hidden: false, },
            { path: '/school/classList', component: classList, name: '班级列表', hidden: true, },
            { path: '/customerService', component: customerService, name: '客服管理', hidden: false },
            { path: '/company', component: company, name: '代理商列表', hidden: false },
        ]
    },
    {
        path: '/',
        component: home,
        name: '业务管理',
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/client', component: client, name: '客户端管理', hidden: false },
            { path: '/message', component: message, name: '通知管理', hidden: false },
            { path: '/card', component: card, name: '卡务中心', hidden: false },

        ]
    },
    {
        path: '/',
        component: home,
        name: '系统管理',
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/commit', component: commit, name: '操作日志', hidden: false },
            { path: '/personal', component: personal, name: '个人中心', hidden: false },
            { path: '/feedback', component: feedback, name: '意见反馈', hidden: false },

        ]
    },
    {
        path: '/',
        component: home,
        name: '权限管理',
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/distribute', component: distribute, name: '权限分配', hidden: false },

        ]
    },
    {
        path: '/',
        component: home,
        name: '监控管理',
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/terminal', component: terminal, name: '终端管理', hidden: false },

        ]
    },
    {
        path: '/',
        component: home,
        name: '数据管理',
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/dataCheck', component: dataCheck, name: '数据查看', hidden: false },
            { path: '/statisticalForm', component: statisticalForm, name: '统计报表', hidden: false },

        ]
    },
    {
        path: '/',
        component: home,
        name: '导航一',
        iconCls: 'el-icon-date',//图标样式class
        children: [
            { path: '/index', component: index, name: '主页', hidden: true },
            { path: '/content', component: content, name: 'axios+分页' },
            { path: '/axiosTest', component: axiosTest, name: 'axiosTest' },
            { path: '/layout', component: layout, name: 'Layout布局' },
            { path: '/echarts', component: echart, name: 'echarts' },
            { path: '/vuex', component: Vuex, name: 'vuex测试' },
        ]
    },
    {
        path: '/',
        component: home,
        name: '导航二',
        iconCls: 'el-icon-setting',//图标样式class
        children: [
            { path: '/table1', component: table1, name: 'table1', hidden: false },
            { path: '/table2', component: table2, name: 'table2', hidden: false },
            { path: '/ueditor', component: Ueditor, name: 'ueditor', hidden: false },
            { path: '/upload', component: upload, name: '头像上传', hidden: false },
            { path: '/vueImgInputer', component: vueImgInputer, name: 'vueImgInputer', hidden: false },
        ]
    },
    // {
    //     path: '*',
    //     hidden: true,
    //     redirect: { path: '/404' }
    // }
]
//实例化vue的时候 只挂着constantRouter
// export default new Router({
//     routes: constantRouterMap
// });
//异步挂载的路由
//动态需要根据权限加载的路由表
// export const asyncRouterMap = [

// ]

export default routers




