import 'babel-polyfill'//兼容ie不支持promise
import Vue from 'vue'//引用vue
import Vuex from 'vuex'//引用vuex
import store from './vuex/store'//引入核心仓库store.js
//使用
Vue.use(Vuex)
//引用路由
import Router from 'vue-router'
//使用路由
Vue.use(Router)

import NProgress from 'nprogress'; // Progress 进度条
import 'nprogress/nprogress.css';// Progress 进度条 样式
import VueImgInputer from 'vue-img-inputer'//上传
Vue.component('VueImgInputer', VueImgInputer)

//引入axios
import axios from 'axios'
//但是其他组件内无法使用axios.将axios改写成Vue的原型属性
Vue.prototype.$axios = axios// 绑定到全局 各个地方都能使用 

//引入elementUI
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
//引入echarts
import echarts from 'echarts'
//使用elementUI
Vue.use(ElementUI)

//引入一些全局需要用的配置文件
import api from './config/api'  //axios封装
Vue.prototype.$api = api// 绑定到全局 各个地方都能使用
import district from '../static/district'  //省市区三级数据文件
Vue.prototype.$district = district// 绑定到全局 各个地方都能使用


//引用入口文件
import App from './App.vue'
//引用路由配置文件
import routes from './config/routes'


//使用配置文件规则 创建router实例，传入routes配置
const router = new Router({
    //如果不配置 mode，就会使用默认的 hash 模式，该模式下会将路径格式化为 #! 开头
    //使用 HTML5 history 模式，该模式下没有 # 前缀，而且可以使用 pushState 和 replaceState 来管理记录
    //HTML5 history 模式的更多内容，可以参考官方文档：https://router.vuejs.org/zh-cn/essentials/history-mode.html
    // mode:'history',      
    routes,
})

router.beforeEach((to, from, next) => {
    NProgress.start(); // 开启Progress
    if (to.path == '/login') {
        sessionStorage.removeItem('user');
    }
    //用户登录后，由服务端生成一个token在浏览器中，
    //前端这边每次路由跳转都会去获取token，如果存在则登录状态存在，则next()继续，否则指到/logIn页面
    //获取本地user登陆信息 
    let user = JSON.parse(sessionStorage.getItem('user'));
    // let testData = JSON.parse(sessionStorage.getItem('testData'));
    if (!user && to.path != '/login') {
        next({ path: '/login' })
        NProgress.done();
    } else {
        // if (testData) {
        //     let temp = false;
        //     // console.log(testData)
        //     // console.log(to.path)
        //     //遍历登陆后 给到的路由信息 
        //     // for(var i=0;i<testData.length;i++){
        //     //     if(testData[i].children){
        //     //         for(var j =0;j<testData[i].children.length;j++){
        //     //             //匹配到当前路由和后台返回的路由
        //     //             if(to.path == testData[i].children[j].path){
        //     //                 // console.log(testData[i].children[j].hidden)
        //     //                 //如果 hidden为true 那么隐藏 并且地址栏中输入给重定向到404
        //     //                 if(testData[i].children[j].hidden){
        //     //                     temp = true;
        //     //                     break;
        //     //                 }
        //     //             }
        //     //         }
        //     //     }
        //     // }
        //     if(temp){  next({ path: '/404' })}
        // }
        next()
        NProgress.done();
        // console.log(router.options.routes)  
        // console.log(router.options)      
    }

})

router.afterEach(() => {
    NProgress.done()
})

Vue.config.productionTip = false


//运行起来
new Vue({
    el: '#app',
    store,
    router,
    render: (h) => h(App)
})


