
<template>
  <div>
    <h1 class="logo">index页面,vue项目测试</h1>
    <ul class="list">
      <li v-for="item in lists">{{item.title}}</li>
    </ul>
    <div class="test">
      <slot>测试插口</slot>
    </div>
    <div class="footer">
      <input type="text" placeholder="输入作者" v-model="newAuthor">
      <button v-on:click="changeAuthor">changeAuthor</button>
      <button v-on:click="setAuthor">setAuthor</button>
      <p>this demo author for {{author}}</p>
    </div>
    <vue-area :props-show="show" :props-result="result" v-on:result="areaResult"></vue-area>
  </div>
</template>
<script>
export default {
  name: 'indexVue',
  data() {
    return {
      newAuthor: "",
      lists: [],
      result: null,
      show: false
    }
  },
  computed: {
    //将store.js里的状态映射到组件
    //主要在computed中,将this.$store.state.author
    author() {
      return this.$store.state.author
    },
    areaResult: function (show, result) {
      this.show = show
      this.result = result
    }
  },
  methods: {
    changeAuthor: function () {
      this.$store.state.author = this.newAuthor
    },
    setAuthor: function () {
      this.$store.commit('newAuthor', this.newAuthor)
    }
  }
}
</script>
<style lang="scss">

</style>


