<template>
    <div id="app1">
        <VueImgInputer v-model="file" accept="image/*" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>default</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" icon="img" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>icon="img"</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" theme="light" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>theme="light"</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" placeholder="请选择身份证" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>placeholder="..."</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" imgSrc="" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>imgSrc="..."</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" readonly imgSrc="" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>imgSrc="..." readonly</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" readonly noMask imgSrc="" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>imgSrc="..." readonly noMask</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" nhe @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>nhe //no-hover-effect</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" size="small" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>size="small"</code>
        </h1>
    
        <VueImgInputer v-model="file" accept="image/*" size="large" @onChange="fileChange"></VueImgInputer>
        <h1>
            <code>size="large"</code>
        </h1>
    
    </div>
</template>

<script>
//   import Hello from './components/Hello'

export default {
    name: 'app1',

    data() {
        return {
            file: null
        };
    },
    methods: {
        fileChange(file, name) {
            console.log('File:', file);
            console.log('FileName:', name);
        }
    }
}
</script>

<style  lang="scss">
#app1 {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
    padding-bottom: 100px;
    .test-inputer {
        margin-top: 50px
    }

    code {
        background: #35495e;
        padding: 5px 20px;
        display: inline-block;
        color: #ffeb59;
        font-size: 28px;
        line-height: 1.5;
        border-radius: 5px
    }

    h1,
    h2 {
        font-weight: 400
    }

    ul {
        list-style-type: none;
        padding: 0
    }

    li {
        display: inline-block;
        margin: 0 10px
    }

    a {
        color: #42b983
    }
    .img-inputer {
        margin-top: 50px;
    }
}
</style>



